@import '../../styles/variables';

.cursor {
  &__inner,
  &__center {
    z-index: 1000;
    mix-blend-mode: difference;
    position: fixed;
    left: 0;
    top: 0;
    transform: translate(-50%, -50%) rotate(45deg);
    width: var(--cursor-size);
    height: var(--cursor-size);
    pointer-events: none;
    user-select: none;
  }
  &__center {
    --cursor-size: 12px;
    background: #ffffff;
  }
  &__inner {
    --cursor-size: 45px;
    border: 1px solid #ffffff;
    background-color: #000000;
  }
}